
.home {

    margin-bottom: 77px;

    .intro {

        height: 100vh;
        width: 100vw;
        background-color: red;
        position: absolute;
        z-index: 1000;

        background-repeat: no-repeat!important;
        background-size: cover!important;
        background-position: center!important;

        &_text {

            font-size: var(--med_font);
            padding: 70px 120px;

            @include mobile {

            padding: 40px 20px;
            display: flex;
            flex-direction: column;
            justify-content: center;
            align-items: center;

            }

            .sub {

                margin-left: 70px;

                @include mobile {

                    margin-left: 0px;

                }

            }

        }

        .logo {

            margin: 0px 30px;
            margin-left: 20px;
            height: 56px;
            position: fixed;
            bottom: 10px;
            left: 10px;
            z-index: 10000000;

            @include mobile {

                margin: 0px;
                height: 36px;

            }

            svg {

                height: 100%;

            }

        }

        .arrow_long {

            display: flex;
            position: absolute;
            width: 100vw;
            bottom: 2px;
            left: 0px;
            justify-content: center;

            // transition: transform 0.3s ease,
            //             opacity 0.3s ease;
            
            animation: 0.7s $Power1EaseOut alternate up;

            svg {

                width: 30px;
                height: 56px;

            }
        }
    }

    @keyframes up {

        0%{
            transform: translateY(0px);
        }
        50%{
            transform: translateY(-20px);
        }
        100% {
            transform: translateY(0px);
        }
    
    }

    .gallery {

        padding-top: 100vh;
        top: 100vh;
        display: flex;
        flex-wrap: wrap;

        .figure_wrapper {

            height: 102vh;
            cursor: pointer;

            .background_image {
    
                width: 100%;
                height: 100%;
                background-size: contain;         
    
            }

            .figure {

                height: 0px;
                overflow: hidden;

                transition: height 2s $Power4EaseOut 0.5s;

            }

        }

        .figure_wrapper:nth-child(2n), .figure_wrapper:nth-child(3n) {
    
            width: 60vw;

            .figure {

                width: 60vw;
                // display: flex;
                // justify-content: center;
                // align-items: center;

                .background_image {

                    background-repeat: no-repeat;
                    background-position: center;

                    width: calc(60vw - 30vw);
                    height: calc(102vh - 30vh);
                    margin: 15vh 15vw;

                }

            }
        }
    
        .figure_wrapper:nth-child(5n),  .figure_wrapper:first-child, .figure_wrapper:nth-child(4n){
    
            width: 40vw;

            .figure {

                width: 40vw;
                display: flex;
                justify-content: left;

                .background_image {

                    width: 40vw;
                    height: 102vh;
                    background-position: center;
                    background-repeat: repeat;
                    margin: 0px;
    
                }

            }
    
        }

        .figure.visualised {

            height: 102vh;

        }
    }
}

.gallery {

    @include mobile {

        flex-direction: column;
    }

    .figure_wrapper {

        @include mobile {

            width: 100vw!important;
            height: calc((100vw * var(--th)) / var(--tw))!important;

        }

    }

    .figure_wrapper:nth-child(2n), .figure_wrapper:nth-child(3n) {

        @include mobile {

            display: flex;
            justify-content: center;
            align-items: center;
            
        }

        .figure {

            @include mobile {

                width: 80vw!important;
                height: calc((80vw * var(--th)) / var(--tw))!important;
                
            }

            .background_image {

                @include mobile {

                    width: 80vw!important;
                    height: calc((80vw * var(--th)) / var(--tw))!important;
                    margin: 0px!important;
        
                }

            }

        }


    }

    .figure_wrapper:nth-child(5n),  .figure_wrapper:first-child, .figure_wrapper:nth-child(4n){

        .figure {

            @include mobile {

                width: 100vw!important;
                height: 100%!important;
    
            }

            .background_image {

                @include mobile {

                    width: 100vw!important;
                    margin: 0px!important;
                    height: 100%!important;
        
                }

            }

        }

    }

}